//dev, debug
@mixin sdf-devColor(
	$outline: 0,
	$backgroundcolor: 0,
	$opacity1: .1,
	$opacity2: $opacity1
) {
	@if $sdfDevMode == 1 and $sdfDevColors == 1 {
		@if $outline !=0 {
			outline: 1px solid rgba($outline,$opacity1);
		}
		@if $backgroundcolor !=0 {
			background-color: rgba($backgroundcolor,$opacity2);
		}
	}
}


//errorMessage
@mixin sdf-errorMessage( $message: '!ERROR' ) {
	position: relative;

	&::after {
		content: "#{$message}";
		color: black;
		background-color: rgba(#fff,.75);
		border: 2px dashed darken(red, 15%);
		max-width: 192px;
		position: absolute;
		font-family: Arial;
		// font-style: italic;
		font-weight: bold;
		padding: 4px;
		border-radius: 2px;
		font-size: 13px;
		letter-spacing: 0.5px;
		top: 8px;
		left: 4px;
		z-index: 1000;
	}
}//errorMessage


//min. Breakpoint
@mixin sdf-minBp( $breakPoint ) {
	@if $breakPoint > 99 {
		$breakPointString: "(min-width: #{$breakPoint}px)";
		@media #{$breakPointString} {
			@content;
		}
	} @else
	if $breakPoint == 1 {
		$breakPointString: "(min-width: #{$bp-1}px)";
		@media #{$breakPointString} {
			@content;
		}
	} @else
	if $breakPoint == 2 {
		$breakPointString: "(min-width: #{$bp-2}px)";
		@media #{$breakPointString} {
			@content;
		}
	} @else
	if $breakPoint == 3 {
		$breakPointString: "(min-width: #{$bp-3}px)";
		@media #{$breakPointString} {
			@content;
		}
	} @else
	if $breakPoint == 4 {
		$breakPointString: "(min-width: #{$bp-4}px)";
		@media #{$breakPointString} {
			@content;
		}
	} @else
	if $breakPoint == 5 {
		$breakPointString: "(min-width: #{$bp-5}px)";
		@media #{$breakPointString} {
			@content;
		}
	}
	@if $breakPoint == 6 {
		$breakPointString: "(min-width: #{$bp-6}px)";
		@media #{$breakPointString} {
			@content;
		}
	}
	@if $breakPoint == 7 {
		$breakPointString: "(min-width: #{$bp-7}px)";
		@media #{$breakPointString} {
			@content;
		}
	}
	@if $breakPoint == 8 {
		$breakPointString: "(min-width: #{$bp-8}px)";
		@media #{$breakPointString} {
			@content;
		}
	}
	@if $breakPoint == 9 {
		$breakPointString: "(min-width: #{$bp-9}px)";
		@media #{$breakPointString} {
			@content;
		}
	}
}//min. Breakpoint


