//minmaxBP
//use with variables eg. @include sdf-minMaxBp($bp-3,$bp-5);
@mixin sdf-minMaxBp( $breakPoint1, $breakPoint2 ) {
	@media (min-width: #{$breakPoint1}px) and (max-width: #{$breakPoint2}px) {
		@content;
	}
}//minmaxBP


//debug-bp
@mixin sdf-devBp {
	@if $sdfDevMode == 1 and $sdfDevBp == 1 {
		&::before {
			content: ' ';
			position: fixed;
			z-index: 100;
			content: 'xs';
			text-transform: uppercase;
			font-weight: 700;
			top: 0;
			left: 0;
			background-color: #eee;
			color: #666;
			padding: 4px;
			font-size: 13px;
			@include sdf-minBp(1) {
				content: '1 - #{$bp-1}px';
			}
			@include sdf-minBp(2) {
				content: '2 - #{$bp-2}px';
			}
			@include sdf-minBp(3) {
				content: '3 - #{$bp-3}px';
			}
			@include sdf-minBp(4) {
				content: '4 - #{$bp-4}px';
			}
			@include sdf-minBp(5) {
				content: '5 - #{$bp-5}px';
			}
			@include sdf-minBp(6) {
				content: '6 - #{$bp-6}px';
			}
			@include sdf-minBp(7) {
				content: '7 - #{$bp-7}px';
			}
			@include sdf-minBp(8) {
